


import axios from "axios";
import { message } from 'antd';

axios.defaults.baseURL = "https://tianjiangyang.top:3000/";


// 拦截器配置    Interceptors 
// 发送请求之前 添加拦截器  
axios.interceptors.request.use(function (config) {
    // 发送之前做的事情 
    message.info('请求中');
    console.log(config);
    return config;
}, function (error) {
    // 发送失败 做的失败 
    message.error('请求失败');
    return Promise.reject(error);
});

// 响应数据 的拦截器 
axios.interceptors.response.use(function (response) {
    // 成功接收到数据 
    console.log(response);
    message.success('请求成功');
    return response;
}, function (error) {
    message.error('响应失败');
    // 无法接收到数据  
    return Promise.reject(error);
});


export  {axios};

